import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import Login from './components/Login';
import TermoCompromisso from './components/TermoCompromisso';
import TermoURL from './components/TermoURL';
import AssinarTermo from './components/AssinarTermo';
import VisualizarTermo from './components/VisualizarTermo';
import Dashboard from './components/Dashboard';

// Componente para proteger rotas que precisam de autenticao
const ProtectedRoute = ({ children }: { children: React.ReactNode }) => {
  const isAuthenticated = localStorage.getItem('token') !== null;
  
  if (!isAuthenticated) {
    return <Navigate to="/" replace />;
  }

  return <>{children}</>;
};

export default function AppRoutes() {
  return (
    <BrowserRouter>
      <Routes>
        {/* Rota pblica de login */}
        <Route path="/" element={<Login />} />

        {/* Rotas protegidas (requerem autenticao) */}
        <Route path="/dashboard" element={
          <ProtectedRoute>
            <Dashboard />
          </ProtectedRoute>
        } />
        
        {/* Criar novo termo */}
        <Route path="/termo/novo" element={
          <ProtectedRoute>
            <TermoCompromisso />
          </ProtectedRoute>
        } />

        {/* Visualizar URL gerada aps criar termo */}
        <Route path="/termo/:id/url" element={
          <ProtectedRoute>
            <TermoURL />
          </ProtectedRoute>
        } />

        {/* Visualizar detalhes do termo */}
        <Route path="/termo/:id" element={
          <ProtectedRoute>
            <VisualizarTermo />
          </ProtectedRoute>
        } />

        {/* Rota pblica para assinar termo */}
        <Route path="/assinar/:id" element={<AssinarTermo />} />

        {/* Redirecionar rotas no encontradas para o dashboard */}
        <Route path="*" element={
          <ProtectedRoute>
            <Navigate to="/dashboard" replace />
          </ProtectedRoute>
        } />
      </Routes>
    </BrowserRouter>
  );
} 