import axios from 'axios';
import https from 'https';

export const API_URL = 'https://apiagilsign.villelatech.com.br';

// URL base do frontend
export const FRONTEND_URL = 'https://ville5113.c44.integrator.host';

const config = {
  baseURL: `${API_URL}/api`,
  timeout: 30000, // 30 segundos
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json'
  },
  httpsAgent: new https.Agent({
    rejectUnauthorized: false // Permite certificados SSL auto-assinados
  })
};

export const api = axios.create(config);

// Interceptor para adicionar o token de autenticao
api.interceptors.request.use((config) => {
  try {
    const token = localStorage.getItem('token');
    if (token && config.headers) {
      config.headers['Authorization'] = `Bearer ${token}`;
      console.log('Token adicionado ao cabealho:', token);
    } else {
      console.log('Token no encontrado no localStorage');
    }
    console.log('Configurao da requisio:', {
      url: config.url,
      method: config.method,
      headers: config.headers,
      data: config.data
    });
    return config;
  } catch (error) {
    console.error('Erro ao processar token:', error);
    return config;
  }
}, (error) => {
  console.error('Erro no interceptor de requisio:', error);
  return Promise.reject(error);
});

// Interceptor para tratar erros de resposta
api.interceptors.response.use(
  (response) => {
    console.log('Resposta recebida:', {
      status: response.status,
      data: response.data,
      headers: response.headers
    });
    return response;
  },
  (error) => {
    console.error('Erro na resposta:', {
      message: error.message,
      response: error.response?.data,
      status: error.response?.status,
      config: error.config
    });
    
    if (error.response?.status === 401) {
      console.log('Token invlido ou expirado, redirecionando para login');
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      window.location.href = '/';
    }
    return Promise.reject(error);
  }
);

export interface TermoData {
  nome: string;
  sobrenome: string;
  email: string;
  equipamento: string;
  equipe: string;
  data: string;
  urlAcesso?: string;
  assinatura?: string;
}

export interface TermoResponse {
  id: string;
  urlAcesso: string;
}

export interface TermoDetalhes {
  id: string;
  nome: string;
  sobrenome: string;
  email: string;
  equipamento: string;
  status: 'pendente' | 'assinado';
  dataCriacao: string;
  urlAcesso: string;
  assinatura?: string;
}

export interface CriarTermoDTO {
  nome: string;
  sobrenome: string;
  email: string;
  equipamento: string;
  status: 'pendente';
}

export interface AtualizarStatusDTO {
  status: 'assinado';
  assinatura: string;
}

interface ApiResponse {
  message: string;
  termo: TermoDetalhes;
}

export const TermoService = {
  // Criar novo termo
  criar: async (data: CriarTermoDTO): Promise<TermoDetalhes> => {
    try {
      console.log('Enviando dados para criar termo:', data);
      
      const response = await api.post<ApiResponse>('/termos', data);
      
      console.log('Resposta completa do servidor:', {
        status: response.status,
        headers: response.headers,
        data: response.data
      });
      
      if (!response.data) {
        throw new Error('Resposta do servidor no contm dados');
      }

      if (!response.data.termo) {
        throw new Error('Resposta do servidor no contm o termo');
      }

      if (!response.data.termo.id) {
        throw new Error('Termo no contm ID');
      }
      
      return response.data.termo;
    } catch (error: any) {
      console.error('Erro detalhado ao criar termo:', {
        message: error.message,
        response: error.response?.data,
        status: error.response?.status,
        config: error.config
      });
      
      if (error.response?.status === 404) {
        throw new Error('Endpoint no encontrado. Verifique a URL da API.');
      }
      
      if (error.code === 'ECONNREFUSED') {
        throw new Error('No foi possvel conectar ao servidor. Verifique se o servidor est rodando.');
      }
      
      throw new Error(error.response?.data?.message || error.message || 'Erro ao criar termo. Por favor, tente novamente.');
    }
  },

  // Buscar termo por ID
  buscarPorId: async (id: string): Promise<TermoDetalhes> => {
    const response = await api.get<TermoDetalhes>(`/termos/${id}`);
    return response.data;
  },

  // Buscar termo por URL de acesso
  buscarPorUrl: async (urlAcesso: string): Promise<TermoDetalhes> => {
    console.log('[API] buscarPorUrl - Iniciando com urlAcesso:', urlAcesso);
    const response = await api.get<TermoDetalhes>(`/termos/acesso/${urlAcesso}`);
    console.log('[API] buscarPorUrl - Resposta:', response.data);
    return response.data;
  },

  // Listar todos os termos
  listar: async (): Promise<TermoDetalhes[]> => {
    const response = await api.get<TermoDetalhes[]>('/termos');
    return response.data;
  },

  // Atualizar status do termo
  atualizarStatus: async (urlAcesso: string, data: AtualizarStatusDTO): Promise<TermoDetalhes> => {
    console.log('[API] atualizarStatus - Iniciando com urlAcesso:', urlAcesso);
    console.log('[API] atualizarStatus - Dados:', data);
    const response = await api.post<TermoDetalhes>(`/termos/assinar/${urlAcesso}`, {
      assinatura: data.assinatura
    });
    console.log('[API] atualizarStatus - Resposta:', response.data);
    return response.data;
  },

  // Excluir termo
  excluir: async (id: string): Promise<void> => {
    await api.delete(`/termos/${id}`);
  },

  // Gerar URL de acesso para assinatura
  gerarUrlAcesso(urlAcesso: string): string {
    return `${API_URL}/termos/assinar/${urlAcesso}`;
  },

  downloadPDF: async (id: string): Promise<Blob> => {
    try {
      const response = await api.get(`/termos/${id}/pdf`, {
        responseType: 'arraybuffer'
      });

      return new Blob([response.data as ArrayBuffer], { type: 'application/pdf' });
    } catch (error) {
      console.error('Erro ao baixar PDF:', error);
      throw error;
    }
  },

  gerarLinkAssinatura: (urlAcesso: string): string => {
    // Usar o domnio atual do frontend
    const baseUrl = window.location.origin;
    console.log('[API] gerarLinkAssinatura - baseUrl:', baseUrl);
    console.log('[API] gerarLinkAssinatura - urlAcesso:', urlAcesso);
    return `${baseUrl}/assinar/${urlAcesso}`;
  },

  gerarLinkVisualizacao: (id: string): string => {
    return `${API_URL}/termos/visualizar/${id}`;
  }
};

export default api;